import React, { useState } from 'react';
import { css } from '@emotion/core';
import { animated, useSpring } from 'react-spring/hooks';
import { Link } from 'gatsby';
import MobileIcon from './MobileIcon';
import { colors, viewports } from '../../utils/variables';

const DesktopNav = ({ isSecondary, navItems }) => {
  const [mobileNavOpen, setMobileNavOpen] = useState(false);
  const toggleMobileNav = () => setMobileNavOpen(!mobileNavOpen);

  const navLinkProps = useSpring({
    color: isSecondary ? colors.blue : '#fff',
    from: { color: isSecondary ? colors.blue : '#fff' },
  });

  const mobileNavProps = useSpring({
    x: mobileNavOpen ? '0' : '100%',
    from: { x: '100%' },
  });

  return (
    <>
      <animated.nav
        css={navCSS}
        style={{
          transform: mobileNavProps.x.interpolate(x => `translateX(${x})`),
        }}
      >
        {navItems.map(navItem => (
          <Link css={navLinkCSS} to={navItem.slug} key={navItem.slug}>
            <animated.span style={navLinkProps}>{navItem.name}</animated.span>
          </Link>
        ))}
      </animated.nav>
      <MobileIcon
        isSecondary={isSecondary}
        toggleMobileNav={toggleMobileNav}
        mobileNavOpen={mobileNavOpen}
      />
    </>
  );
};

export default DesktopNav;

const navCSS = css`
  align-items: center;
  background: ${colors.blue};
  bottom: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  left: 0;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 5;

  @media ${viewports.medium} {
    display: none;
  }
`;

const navLinkCSS = css`
  align-items: center;
  display: flex;
  font-size: 2rem;
  font-weight: 700;
  position: relative;
  text-decoration: none;
  z-index: 1;
`;
