import React from 'react';
import { Link } from 'gatsby';
import { useWindowScrollPosition } from 'the-platform';
import { animated, useSpring } from 'react-spring/hooks';
import { css } from '@emotion/core';
import Logo from './Logo';
import { viewports } from '../../utils/variables';
import MobileNav from './MobileNav';
import Nav from './Nav';

const navItems = [
  {
    name: 'Products',
    slug: '/products',
    children: [
      {
        name: 'Smart Grid Applications',
        slug: '/products/smart-grid-applications',
      },
      { name: 'IoT Network Platform', slug: '/products/iot-network-platform' },
    ],
  },
  { name: 'Partners', slug: '/partners' },
  { name: 'News', slug: '/news' },
  { name: 'About', slug: '/about' },
  { name: 'Contact', slug: '/contact' },
];

const Header = ({ theme }) => {
  if (process.browser) {
    const { y } = useWindowScrollPosition();
    const isSecondary = y > 100 || theme === 'secondary';

    const backgroundProps = useSpring({
      y: isSecondary ? '0%' : '-110%',
      from: { y: '-110%' },
    });

    return (
      <>
        <header css={baseCSS}>
          <animated.div
            css={backgroundCSS}
            style={{
              transform: backgroundProps.y.interpolate(y => `translateY(${y})`),
            }}
          />
          <Link to="/">{isSecondary ? <Logo /> : <Logo theme="white" />}</Link>
          <Nav isSecondary={isSecondary} navItems={navItems} />
          <MobileNav isSecondary={isSecondary} navItems={navItems} />
        </header>
      </>
    );
  }

  const isSecondary = false;

  return (
    <>
      <header css={baseCSS}>
        <animated.div css={backgroundCSS} />
        <Link to="/">{isSecondary ? <Logo /> : <Logo theme="white" />}</Link>
        <Nav isSecondary={isSecondary} navItems={navItems} />
        <MobileNav isSecondary={isSecondary} navItems={navItems} />
      </header>
    </>
  );
};

export default Header;

const baseCSS = css`
  align-items: center;
  display: flex;
  height: 5rem;
  justify-content: space-between;
  left: 0;
  padding: 0 5%;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 1;

  @media ${viewports.medium} {
    padding: 0 3.25rem;
  }
`;

const backgroundCSS = css`
  background-color: #fff;
  bottom: 0;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  z-index: -1;
`;
